<template>
	<div class="position-relative" :class="animate ? 'animete' : ''" style="width: 100%;height:100%;" @click="$emit('click',$event)">
		<img :src="src" class="position-center" style="height:100%;"/>
		<div class="position-center" style="font-size: 22px;word-break: keep-all;opacity: .7;">
			<slot></slot>
		</div>
		<!-- 这里放个选中状态 -->
	</div>
</template>

<script>
	export default {
		props:{
			name: {
				type: String,
				default: ''
			},
			type: {
				type: Number,
				default: 0
			},
			animate: {
				type: Boolean,
				default: false
			},
			select: {
				type: Boolean,
				default: false
			},
			selectType: {
				type: Number,
				default: 0
			}
		},
		computed:{
			src: function(){
				switch(this.type){
					case 0:
					default:
						return require('@/assets/img/hexagon2.png');
					case 1:
						return require('@/assets/img/hexagon.png');
				}
			}
		}
	}
</script>

<style scoped>
	/* .animete:hover {
		opacity: .8;
	} */
	.animete:active {
		opacity: .6;
	}
</style>
