<template>
	<transition
		:css="false"
		appear
		@before-enter="_before_enter"
		@enter="_enter"
		@leave="_leave"
	>
		<div class="position-center flex-center background">
			<div class="position-relative" style="height: 734px;width: 866px;">
				<my-hexagon
					v-for="(hexagon,hexagonIndex) in hexagons.around"
					:key="hexagonIndex"
					:style="hexagon.style"
					:animate="hexagon.animate"
					:type="hexagon.type"
					class="position-absolute"
					:ref="'hexagon_around_'+hexagonIndex"
					@click="()=>_go(hexagon.path)"
				>
					<div v-html="hexagon.title"></div>
				</my-hexagon>
				<div class="position-center" style="width:430px;height:430px;">
					<div ref="inside" class="position-relative" style="height:100%">
						<img src="../../assets/img/halo.png" style="width: 100%;height:100%;"/>
						<my-hexagon
							v-for="(hexagon,hexagonIndex) in hexagons.inside"
							:key="hexagonIndex"
							:style="hexagon.style"
							:animate="hexagon.animate"
							class="position-absolute"
							@click="()=>_go(hexagon.path)"
						>
							<div v-html="hexagon.title"></div>
						</my-hexagon>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	import myHexagon from '@/components/myHexagon.vue';
	import animate from 'velocity-animate/velocity';
	export default {
		data() {
			return {
				hexagons: {
					around: [
						{ title: '<div class="flex-between flex-vertical flex-column"><div>直流</div><div>电阻</div></div>', type: 0, style: 'left:526px;top:73px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '/table2' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>介损</div><div>及</div><div>电容</div></div>', type: 0, style: 'left:395px;top:27px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>绝缘</div><div>电阻</div></div>', type: 0, style: 'left:252px;top:22px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>回路</div><div>电阻</div></div>', type: 0, style: 'left:134px;top:108px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>实验</div><div>周期表</div></div>', type: 1, style: 'left:0;top:220px;width: 200px;height:200px;" ', select: false, selectType: 0, animate: true, path: '/table' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>放电</div><div>电压</div></div>', type: 0, style: 'left:87px;top:414px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>直流</div><div>耐压</div></div>', type: 0, style: 'left:149px;top:524px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>泄露</div><div>电流</div></div>', type: 0, style: 'left:299px;top:588px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>分合</div><div>时间</div></div>', type: 0, style: 'left:452px;top:587px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>交流</div><div>耐压</div></div>', type: 0, style: 'left:579px;top:532px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' },
						{ title: '<div class="flex-between flex-vertical flex-column"><div>动作</div><div>电压</div></div>', type: 0, style: 'left:687px;top:445px;width: 134px;height: 134px;', select: false, selectType: 0, animate: true, path: '' }
					],
					inside: [
						{ title: '蓝图', type: 0, style: 'left:123px;top:95px;width: 104px;height: 104px;', select: true, selectType: 0, animate: true, path: '/imgdetail' },
						{ title: '数据库', type: 0, style: 'left:174px;top:180px;width: 104px;height: 104px;', select: false, selectType: 0, animate: true, path: '' }
					]
					
				}
			}
		},
		components:{
			myHexagon
		},
		methods:{
			_before_enter(){
				let { 
					hexagon_around_0, hexagon_around_1, hexagon_around_2, hexagon_around_3, hexagon_around_4, hexagon_around_5,
					hexagon_around_6, hexagon_around_7, hexagon_around_8, hexagon_around_9, hexagon_around_10,
					inside
				} = this.$refs;

				inside.style.opacity = 0;
				inside.style.transform = 'scale(.5)';
				hexagon_around_4[0].$el.style.display = 'none';
				hexagon_around_4[0].$el.style.opacity = 0;
				hexagon_around_3[0].$el.style.display = 'none';
				hexagon_around_3[0].$el.style.opacity = 0;
				hexagon_around_5[0].$el.style.display = 'none';
				hexagon_around_5[0].$el.style.opacity = 0;
				hexagon_around_2[0].$el.style.display = 'none';
				hexagon_around_2[0].$el.style.opacity = 0;
				hexagon_around_6[0].$el.style.display = 'none';
				hexagon_around_6[0].$el.style.opacity = 0;
				hexagon_around_2[0].$el.style.display = 'none';
				hexagon_around_2[0].$el.style.opacity = 0;
				hexagon_around_6[0].$el.style.display = 'none';
				hexagon_around_6[0].$el.style.opacity = 0;
				hexagon_around_1[0].$el.style.display = 'none';
				hexagon_around_1[0].$el.style.opacity = 0;
				hexagon_around_7[0].$el.style.display = 'none';
				hexagon_around_7[0].$el.style.opacity = 0;
				hexagon_around_0[0].$el.style.display = 'none';
				hexagon_around_0[0].$el.style.opacity = 0;
				hexagon_around_8[0].$el.style.display = 'none';
				hexagon_around_8[0].$el.style.opacity = 0;
				hexagon_around_9[0].$el.style.display = 'none';
				hexagon_around_9[0].$el.style.opacity = 0;
				hexagon_around_10[0].$el.style.display = 'none';
				hexagon_around_10[0].$el.style.opacity = 0;
			},
			_enter(el,done){
				let time = 600/6; // 毫秒
				let { 
					hexagon_around_0, hexagon_around_1, hexagon_around_2, hexagon_around_3, hexagon_around_4, hexagon_around_5,
					hexagon_around_6, hexagon_around_7, hexagon_around_8, hexagon_around_9, hexagon_around_10,
					inside
				} = this.$refs;

				setTimeout(()=>{

					animate(inside,{ opacity: 1, scale: 1 }, { duration: time });

					setTimeout(()=>{
						hexagon_around_4[0].$el.style.display = 'block';
						animate(hexagon_around_4[0].$el,{ opacity: 1 }, { duration: time })
					},time)
					setTimeout(()=>{
						hexagon_around_3[0].$el.style.display = 'block';
						animate(hexagon_around_3[0].$el,{ opacity: 1 }, { duration: time })
						hexagon_around_5[0].$el.style.display = 'block';
						animate(hexagon_around_5[0].$el,{ opacity: 1 }, { duration: time })
					},2*time)
					setTimeout(()=>{
						hexagon_around_2[0].$el.style.display = 'block';
						animate(hexagon_around_2[0].$el,{ opacity: 1 }, { duration: time })
						hexagon_around_6[0].$el.style.display = 'block';
						animate(hexagon_around_6[0].$el,{ opacity: 1 }, { duration: time })
					},3*time);
					setTimeout(()=>{
						hexagon_around_1[0].$el.style.display = 'block';
						animate(hexagon_around_1[0].$el,{ opacity: 1 }, { duration: time })
						hexagon_around_7[0].$el.style.display = 'block';
						animate(hexagon_around_7[0].$el,{ opacity: 1 }, { duration: time })
					},4*time);
					setTimeout(()=>{
						hexagon_around_0[0].$el.style.display = 'block';
						animate(hexagon_around_0[0].$el,{ opacity: 1 }, { duration: time })
						hexagon_around_8[0].$el.style.display = 'block';
						animate(hexagon_around_8[0].$el,{ opacity: 1 }, { duration: time })
					},5*time);
					setTimeout(()=>{
						hexagon_around_9[0].$el.style.display = 'block';
						animate(hexagon_around_9[0].$el,{ opacity: 1 }, { duration: time })
					},6*time);
					setTimeout(()=>{
						hexagon_around_10[0].$el.style.display = 'block';
						animate(hexagon_around_10[0].$el,{ opacity: 1 }, { duration: time, complete: done })
					},7*time);
				},600)
				
			},
			_leave(el,done){
				animate(el, { opacity: 0 }, { duration: 600, complete: done });
			},
			_go(path){
				path && this.$router.push(path);
			}
		}
	}
</script>

<style scoped>
	.background {
		width: 100%;
		height: 100%;
		background-image: url('../../assets/img/back.png');
		background-position: center center;
		background-size: auto 100%;
		color: #fff;
	}
</style>
